@charset "utf-8";

// Font
@import url('https://rsms.me/inter/inter.css');

// Colours
$deep-blue: #374151;
$subtle-grey: #6b7280;
$subtle-grey-dark: #9fa6b2;
$deep-blue-dark: #252f3f;

// Global Variables
$family-sans-serif: 'Inter', sans-serif;
$dark: $deep-blue;
$grey-light: $subtle-grey;
$grey-dark: $subtle-grey-dark;

// Component variables
$card-shadow: 0 1px 3px rgba(0,0,0,0.12), 0 1px 2px rgba(0,0,0,0.24);

$text: $subtle-grey;
$title-weight: 800;
$title-color: $deep-blue;
$subtitle-color: $subtle-grey;

// Only used if navbar is-spaced
$navbar-padding-vertical: 0;
$navbar-padding-horizontal: 2em;

$footer-color: $subtle-grey-dark;
$footer-background-color: $deep-blue-dark;

// Bulma imports
// @import "../../node_modules/bulma/bulma.sass";

@import "../../node_modules/bulma/sass/utilities/_all.sass";
@import "../../node_modules/bulma/sass/base/_all.sass";
@import "../../node_modules/bulma/sass/helpers/_all.sass";
@import "../../node_modules/bulma/sass/elements/button.sass";
@import "../../node_modules/bulma/sass/elements/container.sass";
@import "../../node_modules/bulma/sass/elements/title.sass";
@import "../../node_modules/bulma/sass/elements/icon.sass";
@import "../../node_modules/bulma/sass/elements/tag.sass";
@import "../../node_modules/bulma/sass/components/navbar.sass";
@import "../../node_modules/bulma/sass/components/card.sass";
@import "../../node_modules/bulma/sass/components/media.sass";
@import "../../node_modules/bulma/sass/layout/section.sass";
@import "../../node_modules/bulma/sass/layout/footer.sass";
@import "../../node_modules/bulma/sass/grid/columns.sass";

// Custom classes
.card-border {
    border: none;
}

.card-border:hover {
    border: 1px solid $border;
    // box-shadow: 0 0.5em 1em -0.125em rgba($scheme-invert, 0.1), 0 0px 0 1px rgba($scheme-invert, 0.02);
}

